<template>
	<view>
		<view class="container">
			<scroll-view :style="{'height':height+'px'}">
				<view :animation="ani" :style="{'height':height+'px'}" style="z-index: 20000;position: absolute;top: -50rpx;left: 50rpx;">
					<canvas canvas-id="firstCanvas" style="position: absolute;top: -129px;left: -21px;"></canvas>
				</view>
				<text class="" style="  width: 90%; text-align: center;background: #fff; color: #000;font-size: 40rpx; z-index: 1;">排字一一排字一一排字</text>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				ani: "",
				height: 0,
			};
		},
		onLoad() {
			var _this = this;
			console.log("走了吗")
			uni.getSystemInfo({
				success: (rek) => {
					console.log(rek)
					_this.height = rek.windowHeight
					console.log(_this.height)
					_this.start(_this,2000);
				}
			})
			var context = uni.createCanvasContext('firstCanvas')
			uni.getImageInfo({
				src: "http://106.15.225.146/gen_excl/public/static/upload/avatar/20220329/b10fd2a8a1f127d5ae9570001ba371d1.jpg",
				success: (rek) => {
					console.log(rek.path)
					context.drawImage(rek.path, 0, 0, 200, 200)
					context.save()
					context.draw()
				}
			})
		},
		methods: {
			start(_this,num) {
				var animation = wx.createAnimation({
					duration: num,
					timingFunction: 'linear',
					delay: 1000
				});
				animation.translate(0, _this.height).step()
				this.ani = animation.export()
			}
		}
	}
</script>

<style lang="scss">
	.img {}
</style>
